body {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
	  list-style-type: none;
	  width: 60%;
}

h2 {
	margin: 0;
}

li {
	margin: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

a {
	text-decoration: none;
	color: inherit;
}

p {
    margin: 0;
}

.main-container {
	background-color: #b27bc9;
	color: white;
	height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	background-image: url("triangles.png");
	background-size: cover;
}


.main-header {
	width: 100%;
	text-align: center;
	border-bottom: 1.5px solid #5b4f4f;
	line-height: 0.1em;
	margin: 60px 0 20px;
	font-family: "Helvetica";
	width: 80%;
	color: #454871;
}

.main-header span {
    background: #D5CAEB;
    padding:0 10px;
}

.sub-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	width: 70%;
	margin-top: 20px;
	height: 40%;
	border: 3px double #999;
    background-color: #454871;
}

hr.solid {
	border-left: 2px solid white;
	height: 65%;
    margin-top: 45px;
}

.info-container {
	width: 50%;
	display: -ms-flexbox;
	display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-family: "Courier New";
	padding: 30px;
}

.navbar {
	margin-top: 20px;
}

.nav-links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    font-family: "Verdana";
    width: 100%;
	color: #454871;
}

.nav-links li:hover{
	text-decoration: underline;
}

.main-work-container {
	margin-top: 20px;
	width: 70%;
}

.main-work-container ul {
	width: 100%;
}

.work-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	border: 3px double #999;
	background-color: #454871;
}

.work-title {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	font-family: "Verdana";
    margin-bottom: 10px;
}

.work-title h2:hover {
	text-decoration: underline;
}

.work-skills {
    font-size: 12px;
    margin-bottom: 10px;
}

.work-img {
	width: 30%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.work-img img {
	width: 150px;
	height: 150px;
}

.work-info {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	font-family: "Verdana";
	width: 70%;
}

.work-description {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	line-height: 25px;
    margin-bottom: 10px;
    margin-left: 20px;
}



